/* Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Arial', sans-serif;
  line-height: 1.6;
  background-color: #F3F3F3;
  color: #333;
  padding-bottom: 80px; /* Space for footer */
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

img {
  max-width: 100%;
  height: auto;
}

/* Header & Navbar */
header {
  background-color: #4A148C;
  color: #FFF;
  padding: 20px 0;
  text-align: center;
}

header h1 {
  margin: 0;
  font-size: 2.5rem;
  font-weight: 700;
}

nav {
  margin-top: 10px;
}

nav ul {
  list-style: none;
  display: flex;
  justify-content: center;
  flex-wrap: nowrap; /* Ensure no wrapping */
  gap: 10px; /* Adjust the space between items */
  padding: 0;
}

nav ul li {
  display: inline-block;
}

nav ul li a {
  color: #FFD700;
  text-decoration: none;
  padding: 10px 12px; /* Reduced padding to fit long words */
  border: 2px solid transparent;
  transition: 0.3s ease-in-out;
  font-size: 1.1rem; /* Slightly reduced font size */
  font-weight: 600;
  white-space: nowrap; /* Prevent wrapping */
}

nav ul li a:hover {
  background-color: #FFD700;
  color: #4A148C;
  border-radius: 5px;
  border: 2px solid #FFD700;
}

/* Footer */
footer {
  background-color: #4A148C;
  color: #FFF;
  padding: 10px 0;
  text-align: center;
  position: fixed;
  width: 100%;
  bottom: 0;
}

/* General Section Styling */
h2 {
  font-size: 2rem;
  color: #4A148C;
  margin-bottom: 15px;
}

ul {
  margin-top: 15px;
}

ul li {
  margin-bottom: 10px;
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-bottom: 15px;
}

button {
  background-color: #FFD700;
  color: #4A148C;
  border: none;
  padding: 10px 20px;
  font-size: 1.2rem;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

button:hover {
  background-color: #4A148C;
  color: #FFD700;
}

/* Responsive Design for Mobile */
@media (max-width: 768px) {
  nav ul {
    flex-direction: column; /* Stack vertically on small screens */
    gap: 10px;
  }

  nav ul li a {
    padding: 10px; /* Adjust padding for mobile */
    font-size: 1.2rem;
  }
}
